/** @jsx jsx */
import React from "react";
import RepoCard from "./RepoCard";
import { useFetch } from "./utils"
import { jsx } from "theme-ui"
import { useLocalstorage } from "rooks";
import "./internal.css"

export const useGitHubStars = (
  username,
) => useFetch(`https://api.github.com/users/${username}/starred?per_page=100`);

export const shuffleArray = (array) => {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
}

export const simplifyArray = (items) => {
  return items.map(item => {
    return {
      username: item.owner.login,
      repository: item.name
    }
  })
}

function StarFetcher() {
  // const [data, loading] = useGitHubStars("GodlikePenguin");
  const [starsValue, setStars, removeStars] = useLocalstorage("edwdob.cache.stars", simplifyArray(exampleData));
  const now = Date.now();
  const [lastUpdateValue, setLastUpdate, removeLastUpdate] = useLocalstorage("edwdob.cache.lastUpdate", now)

  const [data, loading] = [starsValue, false];

  if (loading || !data) {
    return <><p>Loading...</p></>;
  }

  if (data.message) {
    return <p sx={{ color: `secondary`, mt: 3, a: { color: `secondary` }, fontSize: [1, 1, 2] }}>
      Could not fetch Github stars, view the full list <a href="https://github.com/GodlikePenguin?tab=stars">here</a> instead
    </p>
  }

  shuffleArray(data)

  return (
    <div style={{display: 'flex'}}>
      {data && data.slice(0, 3).map((d, i) =>
        <div key={i} style={{flex: '1 1 0px'}} className='CardHolder'>
          <RepoCard key={i} username={d.username} repository={d.repository} />
        </div>
      )}
    </div>
  )
}

export default StarFetcher;

